import { Component, ElementRef, HostListener, ViewChild } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  @ViewChild('stickyMenu') menuElement: ElementRef;
  @HostListener('window:scroll', ['$event'])
  @ViewChild("about", { static: false }) about;

  title = 'madiha';
  isShowTNav = false;
  sticky: boolean = false;
  elementPosition: any;

  constructor() { }

  ngOnInit() { }

  //Show and hide Navbar and ToggleNavBar
  showTNav() {
    this.isShowTNav = !this.isShowTNav;
  }

  //Sticky NavBar
  ngAfterViewInit() {
    this.elementPosition = this.menuElement.nativeElement.offsetTop;
  }
  handleScroll() {
    const windowScroll = window.pageYOffset;
    if (windowScroll >= this.elementPosition) {
      this.sticky = true;
    } else {
      this.sticky = false;
    }
  }

  //smooth scroll to the selected div
  scroll(el: HTMLElement) {
    el.scrollIntoView({ behavior: 'smooth', block: 'start' });
  }

  //scroll to aboutView by onclick event in HomeComponent
  navigateTo(element: string) {
    this[element].nativeElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
  }
}
